* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}
.link {
  background: none !important;
  border: none;
  color: #00e;
  cursor: pointer;
  font-size: 0.8rem;
  padding: 0;
}
.link:hover{
  text-decoration: underline;
}

.container {
  margin: 0 auto;
  max-width: 830px;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  width: 100%;
}
.nav {
  background-color: #00897b;
  color: #fff;
  padding: 10px 0;
}
.nav-brand {
  font-size: 1.4rem;
}
.board {
  position: relative;
  margin-top: 20px;
}
.scores {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 20px;
}
.themes {
  text-align: center;
  font-size: .85rem;
}
.theme-link {
  padding: 0 15px;
}
.final-score {
  left: 50%;
  position: absolute;
  top: 10%;
  -webkit-transition: all 0.1s linear 0s;
  -o-transition: all 0.1s linear 0s;
  transition: all 0.1s linear 0s;
}
.final-score-content {
  background: #fff;
  border: 1px solid #ccc;
  -webkit-box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.1);
  left: -50%;
  padding: 40px 50px;
  position: relative;
  text-align: center;
}
.mark {
  opacity: 0.5
}

.tile {
  border: 0;
  height: 40px;
  position: absolute;
  outline: none;
  width: 40px;
  -webkit-transition: bottom 0.05s ease-in 0s;
  -o-transition: bottom 0.05s ease-in 0s;
  transition: bottom 0.05s ease-in 0s;
}
.tile:link, .tile:visited, .tile:hover {
  color: #000;
  text-decoration: none;
}
.tile-a {
  background-color: #0c89dc;
}
.tile-b {
  background-color: #dd514c;
}
.tile-c {
  background-color: #9c27b0;
}
.tile-d {
  background-color: #feeb3b;
}
.tile-e {
  background-color: #5eb95e;
}

/* Classic style */
.classic .tile {
  border: 2px inset #808080;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-family: serif;
  font-size: 2rem;
  -ms-flex-pack: center;
      justify-content: center;
}
.classic .tile-a {
  background-color: #00f;
}
.classic .tile-a::after {
  content: 'A';
}
.classic .tile-b {
  background-color: #f00;
}
.classic .tile-b::after {
  content: 'B';
}
.classic .tile-c {
  background-color: #f0f;
}
.classic .tile-c::after {
  content: 'C';
}
.classic .tile-d {
  background-color: #ff0;
}
.classic .tile-d::after {
  content: 'D';
}
.classic .tile-e {
  background-color: #0ff;
}
.classic .tile-e::after {
  content: 'E';
}





